<template>
  <div class="hello">
      <h1>{{ msg }}</h1>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: '欢迎访问我的个人网站'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .hello {
    background-color: #ffffff;
    width: auto;
    height: 100px;
    margin: 0;
    padding: 0;
  }
  h1 {
    color: #2C3E50;
  }
</style>



